@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.page__body {
  min-width: 960px;
  margin: 0;
  padding: 0;
  font-family: '"Open Sans", Arial, sans-serif';
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
}

.button {
  font: inherit;
  text-align: center;
  color: #444444;
  background-color: #ffffff;
  vertical-align: middle;
  text-transform: uppercase;
  border: 2px #444444 solid;
  box-sizing: border-box;
}
.button:hover, .button:focus {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}